import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '@/components/Home'
import Note from '@/components/Note'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/note/',
      name: 'home',
      component: Home
    },
    {
      path: '/note/(.*)',
      name: 'Note',
      component: Note
    }
  ]
})



